import uCharts from '../../components/u-charts/u-charts.js';
import uniList from "@/components/uni-list/uni-list.vue"
import uniListItem from "@/components/uni-list-item/uni-list-item.vue"
var _self;

export default {
	components: {
		uniList,
		uniListItem
	},

	data() {
		return {
			cWidth3: '', //圆弧进度图
			cHeight3: '', //圆弧进度图
			arcbarWidth: '', //圆弧进度图，进度条宽度,此设置可使各端宽度一致
			pixelRatio: 1,
		}
	},
	onLoad() {
		_self = this;












		this.cWidth3 = uni.upx2px(450);
		this.cHeight3 = uni.upx2px(450);
		this.arcbarWidth = uni.upx2px(34);
	},
	onReady() {
		this.fillData();
	},
	methods: {

		fillData() {
			var epoint = 2500
			let Arcbar3 = {
				series: [{
					name: '/5,000 epoint',
					data: epoint / 5000,
					color: '#E41F30',
					epoint: epoint
				}]
			};

			this.showArcbar3("canvasArcbar3", Arcbar3);
		},

		showArcbar3(canvasId, chartData) {
			new uCharts({
				$this: _self,
				canvasId: canvasId,
				type: 'arcbar',
				fontSize: 11,
				title: {
					name: Math.round(chartData.series[0].epoint),
					// name: Math.round(1000),
					color: chartData.series[0].color,
					fontSize: 25 * _self.pixelRatio
				},
				subtitle: {
					name: chartData.series[0].name,
					color: '#666666',
					fontSize: 16 * _self.pixelRatio,
					offsetX: 17,
					offsetY: 10
				},
				extra: {
					arcbar: {
						type: 'circle', //整圆类型进度条图
						width: _self.arcbarWidth * _self.pixelRatio, //圆弧的宽度
						startAngle: 0.5 //整圆类型只需配置起始角度即可
					}
				},
				background: '#FFFFFF',
				pixelRatio: _self.pixelRatio,
				series: chartData.series,
				animation: true,
				width: _self.cWidth3 * _self.pixelRatio,
				height: _self.cHeight3 * _self.pixelRatio,
				dataLabel: true,
			});
		},
		toRecords:function(){
			uni.navigateTo({
				url: './epoint/records',
			});
		},
		toRedeem:function(){
			uni.navigateTo({
				url: './epoint/redeemRewards',
			});
		}
	}
}
